.text-center_3 {
    margin-bottom: 600px;
    text-align: center;
  
    h1 {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 400;
      font-size: 36px;
      margin-top: 700px;
      color: #4E4E4E;
    }
  
    .line_1_3 { 
      width: 90px;
      height: 1.5px;
      border: none;
      margin: 10px 0px 5px 714px;
      background: $main-color;
    }
  
    .line_2_3 { 
      width: 90px;
      height: 1.5px;
      border: none;
      margin: 0px 0px 0px 728px;
      background: $main-color;
    }
}

.posts {
    text-align: center;
    margin-top: -590px;
    margin-bottom: 200px;

    .post-img {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 10px;
    }

    .post {
        width: 263px;
        height: 208px;
        padding: 35px 33px 0px 0px;
    }

    .text-upr {
        margin-top: 8px;
        margin-left: -30%;
        font-family: 'Montserrat';
        font-weight: 700;
        font-size: 17.9885px;
        color: #717171;
        cursor: pointer;
        transition: 0.5s;
    }

    p {
        margin-top: -1px;
        margin-left: -33%;
        font-family: 'Montserrat';
        font-weight: 300;
        font-size: 13.991px;
        color: #777777;
        opacity: 0.5;

        span {
            margin-left: 10px;
        }
    }

    .date {
        width: 70px;
        height: 68px;
        position: absolute;
        margin-top: 35px;
        margin-left: 193px;
        background: #203E38;
        transition: 0.5s;

        .text {
            font-family: 'Montserrat';
            font-weight: 400;
            font-size: 16px;
            margin-top: 10px;
            color: #fff;
        }

        .text-2 {
            font-family: 'Montserrat';
            font-weight: 400;
            font-size: 16px;
            margin-top: 10px;
            color: #fff;
