@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.bg-img-5 {
    width: 100%;
    height: 883px;
}

.about-block {
    width: 100%;
    height: 853px;
    margin-top: -800px;
    text-align: center;
    margin-bottom: 400px;
}

.about {
    width: 1160px;
    margin: auto;
    position: relative;
}

.text-main-center {
    font-family: 'Montserrat';
    font-weight: 400;
    font-size: 36px;
    color: #fff;
}

.lines {
    width: 100%;
    height: 20px;
}

.lines::before {
    content: "";
    border-bottom: 1px solid #18CFAB;
    display: block;
    width: 90px;
    height: 1.5px;
    margin-top: 26px;
    margin-left: 530px;
}

.lines::after {
    content: "";
    border-bottom: 1px solid #18CFAB;
    display: block;
    width: 90px;
    height: 1.5px;
    margin-top: 5px;
    margin-left: 545px;
}

.quotes {
    width: 40px;
    height: 28px;
    text-align: center;
    position: absolute;
    margin-left: 557px;
    margin-top: 30px;
}

.review-wrapper {
    width: 1200px;
    height: 60px;
    margin-top: 110px;
    overflow: hidden;    
}

.review-slider-line {
    width: 100%;
    height: 250px;
    position: relative;
    top: 0px;
}

.review {
    font-family: 'Montserrat';
    font-weight: 300;
    font-size: 16px;
    line-height: 30px;
    color: #a5a5a5;
    transition: 2s;
}

.name-slider-wrapper {
    margin: auto;
    margin-top: 37px;
    width: 180px;
    height: 60px;
    overflow: hidden;    
}

.name-slider-line {
    width: 180px;
    height: 240px;
    position: relative;
    top: 0;    
}

.user-box {    
    width: 180px;
    height: 60px;
    transition: 2s;
}

.user-name-1 {
    text-transform: uppercase;
    font-family: 'Montserrat';
    font-weight: 700;
    font-size: 16px;
    color: $main-color;
}

.user-job-1 {
    margin-top: 5px;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    font-size: 12px;
    color: #BBBBBB;
    opacity: 0.4;
}

