.container {
    margin: auto;
    margin-top: 33px;
    width: 800px;
    height: 166px;
}

.slider-wrapper {    
    margin: auto;    
    width: 166px;
    height: 166px;
    border-radius: 100px;
    overflow: hidden;    
}

.large-photo-slider-line {
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 664px;
    height: 166px;
    position: relative;
    left: 0;   
}

.large-photo {
    width: 166px;
    height: 166px;
    border-radius: 100px;
    background: #1fdab531;
    opacity: 0;
    transition: 2s;    
}

.lp-active{
    opacity: 100;
}

.people-slider-img {
    width: 150px;
    height: 150px;
    margin-top: 8px;
    border-radius: 100px;
}

.slider-box{
    margin: auto;
    margin-top: 45px;
    width: 487px;
    height: 90px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.photos-wrapper {
    width: 353px;
    height: 90px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .circle {
        position: absolute;
        width: 70px;
        height: 71px;
        border-radius: 50px;
        background: #1FDAB5;
    }
}

.photo {
    width: 61px;
    height: 61px;
    display: inline-block;
    transition: 0.5s;
    cursor: pointer;
}

.active-photo {
    margin-bottom: 20px;
} 

.active {
    display: block;
}

.people-img_2 {
    width: 65px;
    height: 65px;
    margin-top: 5px;
    margin-left: 2px;
    border-radius: 50px;
}

.slider-button{
    width: 31px;
    height: 31px;
    border: 1px solid #BBBBBB;
    transition: 0.5s;
    cursor: pointer;

    .arroy-1 {
        width: 10px;
        height: 15px;
        margin-top: 8px;
        margin-left: -2px;
        transform: scale(-1);
    }
    
    .arroy-2 {
        width: 10px;
        height: 15px;
        margin-top: 8px;
        margin-left: 4px;
    }

    &:hover {
        background-color: $main-color;
    }
}