.purchase {
    margin-top: -90px;
    margin-bottom: 250px;

    img {
        width: 100%;
        height: 476px;
    }

    .text-cr {
        margin-bottom: 300px;

        .text {
            margin-top: -380px;
            text-align: center;
            text-transform: uppercase;
            font-family: 'Montserrat';
            font-weight: 700;
            font-size: 40px;
            color: $main-color;
        }

        .text-2 {
            text-align: center;
            font-family: 'Montserrat';
            font-weight: 400;
            font-size: 16px;
            color: #D8D8D8;
            opacity: 0.7;
        }
    }

    .main-button {
        margin-top: -58px;
        text-align: center;
      
        .btn-2 {
          border: none;
          border-radius: 4px;
          align-items: center;
          text-transform: uppercase;
          font-family: 'Montserrat';
          font-style: normal;
          font-weight: 700;
          font-size: 15px;
          width: 169px;
          height: 63px;
          color: #fff;
          cursor: pointer;
          background: #00CBF6;
        }
    }
      
    .rectangle {
        margin-top: -185px;
        margin-left: -1px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 32px;
        cursor: pointer;

        .rectangle-2 {
            width: 169px;
            height: 63px;
        }
    }
}