@import "./assets/variable";
@import "./components/header";
@import "./components/footer";
@import "./components/tab";
@import "./components/block";
@import "./components/home";
@import "./components/purchase";
@import "./components/work";
@import "./components/news";
@import "./components/about";
@import "./components/gallery";

@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

* {
  margin: 0;
  padding: 0;
}

.bg-img {
  width: 100%;
  height: 750px;
}

@media only screen and (max-width: 1200px) {

  body {
    overflow-x: hidden;
  }
  
  .nav {

    .serch {
      margin-left: 93%;
    }

    .logo {
      svg {
        margin-left: -50px;
      }
    }
  
    .text-logo {
      img {
        margin-left: -50px;
      }
    }
  
    .text-logo_2 {
      span {
        margin-left: -48px;
      }
    }
  
    .text-logo_2-1 {
      span {
        margin-left: -47px;
      }
    }
  
    .menu {
      margin-left: 430px;
    }
  }

  .block-finger {
    width: 300px;
    height: 359px;

    .finger {
      margin-left: 117px;
    }
  }

  .block-diamond {
    width: 300px;
    height: 359px;
    margin-left: 300px;

    .diamond {
      margin-left: 108px;
    }
  }

  .block-bulb {
    width: 300px;
    height: 359px;
    margin-left: 600px;

    .bulb {
      margin-left: 124px;
    }
  }

  .block-device {
    width: 300px;
    height: 359px;
    margin-left: 900px;

    .device {
      margin-left: 108px;
    }
  }

  .text-center {
    .line_1_1 {
      margin-left: 47%;
    }

    .line_2_1 {
      margin-left: 48%;
    }
  }

  .tabs {
    max-width: 600px;
    margin: 200px auto;
    margin-top: -430px;
    margin-left: 27px;

    .tabs__nav {
        display: flex;

        .tabs__nav-btn {
            white-space: nowrap;
            font-family: 'Montserrat';
            font-weight: 400;
            padding: 30px 25px;
            font-size: 20px;
            background: #F8FCFE;
            border: 1px solid #EDEFEF;
            color: #717171;
            cursor: pointer;

            &:hover {
                border: 1px solid $main-color;
            }
        }

        .tabs__nav-btn.active {
            background: $main-color;
            color: #fff;
        }
    }

    .arrow {
        width: 300px;
        margin: 0 auto;
        padding: 10px 10px;
        position: relative;
        background: $main-color; 
        text-align: center;
    }
    
    .arrow:before {
        content: ""; 
        border: solid transparent;
        position: absolute;
        top: 100%;
        left: 50%;
        border-top-color: $main-color;
        border-width: 12px;
        margin-left: -9px;
    }
}

.tabs__content {
    padding: 20px;
    background-color: #fff;

    .tabs__item {
        display: none;

        .text {
            width: 930px;
            height: 139px;
            margin-top: 8px;
            margin-left: 200px;
            font-family: 'Montserrat';
            font-weight: 300;
            font-size: 17px;
            color: #777777;
            opacity: 0.6;
        }

        .design {
            margin-top: -140px;
            margin-left: -20px;
        }

        .graffic-design {
            margin-top: -140px;
            margin-left: -20px;

            img {
                width: 192px;
                height: 139px;
            }
        }

        .online-support {
            margin-top: -140px;
            margin-left: -20px;

            img {
                width: 192px;
                height: 139px;
            }
        }

        .app-design {
            margin-top: -140px;
            margin-left: -20px;

            img {
                width: 192px;
                height: 139px;
            }
        }

        .online-marketing {
            margin-top: -140px;
            margin-left: -20px;

            img {
                width: 192px;
                height: 139px;
            }
        }

        .seo-service {
            margin-top: -140px;
            margin-left: -20px;

            img {
                width: 192px;
                height: 139px;
            }
        }
    }
    
    .tabs__item.active {
        display: block;
    }
  }

  .text-center_2 {   
    .line_1_1 {
      margin-left: 46%;
    }

    .line_2_2 {
      margin-left: 47%;
    }
  }

  .text-center_3 {
    .line_1_3 {
      margin-left: 46%;
    }

    .line_2_3 {
      margin-left: 47%;
    }
  }

  .posts {
    margin-left: 33px;
  }

  .review-wrapper {
    width: 1100px;
    height: 60px;
    margin-top: 110px;
    overflow: hidden;    
  }

  .review {
    margin-left: 60px;
    font-family: 'Montserrat';
    font-weight: 300;
    font-size: 16px;
    line-height: 30px;
    color: #a5a5a5;
    transition: 2s;
  }

  .text-center_6 {
    .line_1_6 {
      margin-left: 46.5%;
    }

    .line_2_6 {
      margin-left: 47.5%;
    }
  }

  .Gallery {
    .gallery-img {
      margin-left: -180px;
    }

    .loaded-img {
      margin-left: -180px;
    }
  }
}

