    }
}

.tabs {
    max-width: 600px;
    margin: 200px auto;
    margin-top: -430px;
    margin-left: 120px;

    .tabs__nav {
        display: flex;

        .tabs__nav-btn {
            white-space: nowrap;
            font-family: 'Montserrat';
            font-weight: 400;
            padding: 30px 37px;
            font-size: 20px;
            background: #F8FCFE;
            border: 1px solid #EDEFEF;
            color: #717171;
            cursor: pointer;

            &:hover {
                border: 1px solid $main-color;
            }
        }

        .tabs__nav-btn.active {
            background: $main-color;
            color: #fff;
        }
    }

    .arrow {
        width: 300px;
        margin: 0 auto;
        padding: 10px 10px;
        position: relative;
        background: $main-color; 
        text-align: center;
    }
    
    .arrow:before {
        content: ""; 
        border: solid transparent;
        position: absolute;
        top: 100%;
        left: 50%;
        border-top-color: $main-color;
        border-width: 12px;
        margin-left: -9px;
    }
}

