.text-center_2 {
    margin-bottom: 600px;
    text-align: center;
  
    h1 {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 400;
      font-size: 36px;
      margin-top: -100px;
      color: #4E4E4E;
    }
  
    .line_1_1 { 
      width: 90px;
      height: 1.5px;
      border: none;
      margin: 10px 0px 5px 714px;
      background: $main-color;
    }
  
    .line_2_2 { 
      width: 90px;
      height: 1.5px;
      border: none;
      margin: 0px 0px 0px 728px;
      background: $main-color;
    }
}

.container {
	max-width: 500px;
	min-height: 16em;
	margin: 1em auto 0;
  background: rgba(0, 0, 0, 0.0001);
}

.container--tabs {
  margin-left: -31%;
  white-space: nowrap;
	list-style: none;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
}
.container--tabs .tab {
  width: 582px;
  height: 30px;
  font-family: 'Montserrat';
  font-weight: 400;
	cursor: pointer;
  text-align: center;
	position: relative;
	display: block;
	margin: -1020px -1px 0 0;
	padding: 20px 31px 15px;
	font-size: 18px;
  line-height: 22px;
  border: 1px solid #DADADA;
  color: #717171;

  &:hover {
    border: 1px solid $main-color;
  }
}

.container--tabs .tab--active {
	background: #fff;
	color: $main-color;
	pointer-events: none;
  border: 2px solid $main-color;
}

.content {
	display: none;
  cursor: pointer;
  white-space: nowrap;
  margin-top: -80%;
  margin-left: -65%;
}

.content--active {
	display: block;
}

.blurfilter {
	margin: -8px -5px 0px 0px;
	padding: 0;
	display: inline-block;
	position: relative;
	overflow: hidden; 
}

.blurfilter img {
	width: 285px; 
	height: 225px;
	transition: all 0.4s 0.4s;
  margin: -4px 1px 0px 0px;
  transition: all .5s;
}

.blurfilter span {
	position: absolute;
	display: block;
	text-align: left;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
	text-align: center;
	background: #fff;
	z-index: 100;
	width: 285px;
	height: 220.1px;
	overflow: hidden;
	top: 0;
	left: 0;
	font-size: 14px;
	opacity: 0;
	--moz-transition: all 0.4s;
	--webkit-transition: all 0.4s;
	transition: all .4s;
}


.blurfilter {

  .txt {
    margin: 0 auto;

    .txt-1 {
      text-align: center;
      margin-top: 30px;
      text-transform: uppercase;
      font-family: 'Montserrat';
      font-weight: 700;
      font-size: 18px;
      color: $main-color;
    }

    p {
      margin-top: 3px;
      text-align: center;
      font-family: 'Montserrat';
      font-weight: 300;
      font-size: 16px;
      color: #8A8A8A;;
    }

    hr {
      width: 100%;
      height: 2px;
      background-color: $main-color;
      border: none;
    }
  }

  .circle {
    width: 40px;
    height: 40px;
    margin-top: 16%;
    margin-left: 34%;
    border: 1px solid $main-color;
    border-radius: 30px;

    svg {
      width: 20px;
      height: 20px;
      margin-top: 24%;
      margin-left: 2.3px;
    }
  }

  .circle-2 {
    width: 40px;
    height: 40px;
    margin-top: -14.8%;
    margin-left: 52%;
    border: 1px solid $main-color;
    background-color: $main-color;
    border-radius: 30px;

    img {
      width: 14px;
      height: 13px;
      margin-top: 33%;
      margin-left: 1.7px;
    }
  }
}

.blurfilter:hover img {
	--moz-transition: all 0.4s;
	--webkit-transition: all 0.4s;
}

.blurfilter:hover span {
	opacity: 1;
	box-shadow: 0 0 5px gray;
	--moz-transition: all .4s .4s;
	--webkit-transition: all .4s .4s;
}

.load-more {
  margin-top: 55px;
  margin-bottom: 100px;
  margin-left: -1%;
  text-align: center;

  .load {
    text-transform: uppercase;
    font-family: 'Montserrat';
    font-weight: 700;
    font-size: 14px;
    width: 168px;
    height: 63px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    background-color: $main-color;
    color: #fff;

    span {
      cursor: pointer;
      margin-left: 35px;
    }
  }

  .plus {
    margin-top: -44px;
    margin-left: -100px;

    .plus-img {
      width: 18px;
      height: 19px;
      cursor: pointer;
    }
  }
}

.loaded {
  display: none;
}

.end-load {
  display: block;
}