.main-text {
    margin-top: -550px;
    text-align: center;
    font-family: 'Montserrat';
    font-size: 16px;
    font-weight: 700;
  
    .text {
      color: #fff;
      text-transform: uppercase;
    }
  
    .text-2 {
      color: $main-color;
    }
  
    .Big-text {
      color: #fff;
      font-size: 70px;
      margin-top: 22px;
      text-transform: uppercase;
    }
  
    .Smoll-text {
      margin-top: 10px;
      font-family: 'Montserrat';
      font-weight: 400;
      font-size: 26.5px;
      color: #D4D4D4;
      opacity: 0.8;
    }
}
  
.main-button {
    margin-top: -56px;
    text-align: center;
  
    .btn-1 {
      margin: 0px 26px 0px 0px;
      border: none;
      border-radius: 4px;
      align-items: center;
      text-transform: uppercase;
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 700;
      font-size: 15px;
      width: 169px;
      height: 63px;
      color: #fff;
      cursor: pointer;
      background: #18CFAB;
    }
  
    .btn-2 {
      border: none;
      border-radius: 4px;
      align-items: center;
      text-transform: uppercase;
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 700;
      font-size: 15px;
      width: 169px;
      height: 63px;
      color: #fff;
      cursor: pointer;
      background: #00CBF6;
    }
}
  
.rectangle {
    margin-top: 130px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 32px;
    cursor: pointer;
}