@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap');

* {
    margin: 0;
    padding: 0;
}

.header {
    width: 100%;
    height: 90px;
    position: absolute;
    background: rgba(94, 93, 88, 0.6);

    .logo {
        margin-top: -50px;
        margin-left: 250px;
    }
}

.nav {

    .text-logo {
        margin-top: -50px;
        margin-left: 266px;
    }

    .text-logo_2 {
        margin-top: -30px;
        margin-left: 315px;
        font-family: 'Montserrat';
        font-size: 23px;
        font-weight: 300;
        color: #fff;
    }

    .text-logo_2-1 {
        margin-top: -27px;
        margin-left: 355px;
        font-family: 'Montserrat';
        line-height: -27px;
        font-size: 23px;
        font-weight: 700;
        color: $main-color;
    }

}

.menu {
    display: flex;
    list-style: none;
    margin: 2px 0px 0px 0px;
    margin-left: 37.3%;
    gap: 10px;
}

.serch {
    width: 20px;
    margin-top: 37px;
    margin-left: 82.5%;
    cursor: pointer;
}
  
.link {
    font-family: 'Montserrat';
    font-weight: 700;
    font-size: 13.5px;
    float: inline-start;
    position: relative;
    margin: 15px;
    margin-top: -22px;
    text-decoration: none;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.6);
}

.link-02:hover {
    transition: .5s;
    color: $main-color;
}
  
.link:after {
    content: '';
    display: block;
    position: absolute;
    bottom: -35px;
    left: 50%;
    height: 3px;
    width: 0;
    color: $main-color;
    background: transparent;
}
  
.link:after {
    transition: width .5s ease, background-color .5s ease, left .5s ease;
}
  
.link:hover:after {
    width: 100%;
    left: 0;
    background: $main-color;
}

.link:after {
    left: 0;
    bottom: -100;
    width: 100%;
    height: 1px;
    background: $main-color;
    transition: transform .5s ease;
    transform: scaleX(0);
}
  
.link:hover:after {
    transform: scaleX(1);
}